<script setup lang="ts">
import { ref } from 'vue'
import { onLoad } from '@dcloudio/uni-app'

import type { BannerItem, CategoryItem } from '@/types/home'
import { getHomeBannerApi, getHomeCategoryMutliApi } from '@/services/home'

import CustomNavBar from './cpns/CustomNavBar.vue'
import CategoryPanel from '.'

const bannerList = ref<BannerItem[]>([])
const getBannerList = async () => {
  const { result } = await getHomeBannerApi<BannerItem[]>()
  bannerList.value = result
}

const categoryList = ref<CategoryItem[]>([])
const getCategoryList = async () => {
  const { result } = await getHomeCategoryMutliApi<CategoryItem[]>()
  categoryList.value = result
}

onLoad(() => {
  getBannerList()
  getCategoryList()
})
</script>

<template>
  <CustomNavBar />
  <XtxSwiper :list="bannerList" />
  <CategoryPanel :list="categoryList" />
</template>

<style lang="scss"></style>
